<template>
  <div>
    <breadcrumb-wrapper>
      <template v-slot:left>
        <el-breadcrumb class="app-breadcrumb" separator="/">
          <transition-group name="breadcrumb">
            <el-breadcrumb-item key="1">
              <router-link :to="'/studio?id='+sid">{{title}}工作室</router-link>
            </el-breadcrumb-item>
            <el-breadcrumb-item key="2">
              文章列表
            </el-breadcrumb-item>
          </transition-group>
        </el-breadcrumb>
      </template>
    </breadcrumb-wrapper>
    <el-card class="box-card">
      <div class="w750 flex-space-between" style="border:none;">
        <div class="user FLEX">


          <avatar :headIcon='user.user_headicon' :bgColor='user.user_backcolor' :name='user.user_abbr' size="small"
                  style="margin: 0 auto;"></avatar>

          <div class="user-info">
            <div class="user-name time">{{user.user_name}}</div>
            <div class="time">该课题共发表（{{total}} 篇）文章</div>
          </div>
        </div>
       <!-- <div class="Btn add-article">

          <router-link :to="{ path: '/addArticle' , query:{sid:sid,fromPage:'Article'}}">添加文章</router-link>

        </div>-->
      </div>
      <div v-if="articlemines.length > 0">
        <div class="section" v-for="item  in articlemines" :key="item.id">
          <div class="w750">
            <div class="section-header flex-space-between">
              <div class="section-left h1">
                <router-link class="ellipsis" :to="'/articleDetail/'+item.id+'?fromPage=Article'">
                  <span v-html="item.title"></span>
                </router-link>
              </div>
              <!-- <div class="section-right">
              <div class="app-img-min-block">

              </div>
              </div>-->
            </div>
            <div class="section-main">
              <router-link class="ellipsis" :to="'/articleDetail/'+item.id+'?fromPage=Article'"> <span v-html="item.brief"></span>
              </router-link>

            </div>
            <div class="section-footer time"> {{item.mtime}}发布</div>
          </div>
        </div>
      </div>

      <div class="time nulldata-txt" style="padding-bottom: 100px;" v-else>
        <tip txt="暂无内容" oder="3"></tip>
      </div>

    </el-card>
  </div>
</template>

<script>
  import {mapGetters} from "vuex";
  import BreadcrumbWrapper from '@/layout/BreadcrumbWrapper'
  import Avatar from '@/components/Avatar/index'
  import {articlelist, getuser, subjectget} from '@/api/index'
  import Tip from '@/components/Tip/index'

  export default {
    name: "Article",
    components: {
      BreadcrumbWrapper,
      Avatar,
      Tip
    },
    computed: {
      ...mapGetters(["name"])
    },
    data() {
      return {

        sid: this.$route.query.id,
        uid: this.$route.query.uid,
        limit: 1000,
        page: 1,
        user: {},
        articlemines: [],
        total: 0,
        title: '',


      };
    },
    mounted() {
      this.articlelist()
      this.getuser()
      this.subjectGet()

    },
    methods: {
      subjectGet() {
        subjectget({
          id: this.sid
        }).then((res) => {

          this.title = res.data.title
        })
      },
      getuser() {

        getuser({uid: this.uid}).then((res) => {
          this.user = res.data


        })

      },
      articlelist() {
        articlelist({
          sid: this.sid,
          uid: this.uid,

          limit: this.limit,
          page: this.page
        }).then((res) => {
          this.articlemines = res.data.list


          this.total = res.data.total

        })

      },

    }
  };
</script>

<style lang="scss" scoped>
  .section {
    background: #fff;
    padding: 0px 0px 10px 0px;
  }

  .w750 {
    padding: 30px 0px 10px 0px;
    border-bottom: #eeeeee 1px solid;
  }

  .section-footer {
    color: rgba(0, 0, 0, 0.447058823529412);
  }

  .section-footer .nick {
    color: #1890ff;
  }

  .section-footer .updata-time {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.247058823529412);
    margin-left: 20px;
  }

  .section-main,
  .section-footer {
    margin-top: 15px;
  }

  .section-main {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: break-all;

  }

  .section-main > a {

  }

  .user-info > div {
    margin: 10px 0px;
  }

  .add-article {
    margin-top: 20px;
  }

  .user-info {
    margin-left: 10px;
  }
</style>
<style scoped>
  .box-card >>> .el-card__body {
    min-height: 550px;
  }

</style>



